/* src/styles/Slider.css */

@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700,900');

body {
  font-family: 'Lato', sans-serif;
  color: #d6f9dd;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.caption {
  position: absolute;
  top: 60%;
  left: 1rem;
  text-align: left;
  z-index: 9;
    color: #fff;
    
  transform: translateY(-50%);
  opacity: 1;
  transition: 500ms ease opacity, 500ms ease transform;
  transform: translateY(60px);
}

.caption.current-caption {
  transition-delay: 1000ms;
  opacity: 1;
  transform: translateY(0);
}

.caption.previous-caption {
  transform: translateY(-60px);
}

.caption-heading {
  transition: 500ms ease-in all;
}

.caption-heading h1 {
  font-size: 4rem;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  text-indent: -0.2rem;
  letter-spacing: 0.2rem;
  font-weight: 300;
}

.caption-subhead {
  font-size: 1rem;
  font-weight: 300;
  text-transform: uppercase;
  color: white;
  letter-spacing: 4px;
  word-spacing: 0.1rem;
  margin-bottom: 2.5rem;
  display: block;
}

button.btn {
opacity: 1;

  text-decoration: none;
  border-radius: 50px;
  padding: 0.5rem 1rem;
  
  letter-spacing: 0.2rem;
  position: relative;
  z-index: 9;
  transition: 250ms ease-in background-color, 500ms ease-in color;
}

button.btn:hover {

  color: white;
}

.container {
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
}

.left-col, .right-col {
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.left-col {
  background-size: cover;
  background-position: center top;
  overflow: hidden;
  position: relative;
}

.left-col .slide {
 
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: left top !important;
  background-size: cover !important;
  background-repeat: no-repeat;
  opacity: 0;
  transition: 1000ms cubic-bezier(1.000, 0.040, 0.355, 1.000) transform, 
              1000ms cubic-bezier(0.680, -0.550, 0.265, 1.550) clip-path;
  transform: translateY(-100%) scale(1);
  z-index: -1;
}

.left-col .slide.previous {
  z-index: 1;
  opacity: 1;
  transform: translateY(0);
  animation-delay: 1s;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  transition: 3s ease transform;
  will-change: transform;
  overflow: hidden;
  object-fit: cover;
}

.left-col .slide.previous.change {
  transform: translateY(50%);
}

.left-col .slide.next {
  transform: translateY(-100%);
  z-index: 3;
  opacity: 1;
  clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%);
  overflow: hidden;
  object-fit: cover;
}

.left-col .slide.current {
  opacity: 1;
  transform: translateY(0) scale(1.25);
  z-index: 2;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  overflow: hidden;
  object-fit: cover;
}

.right-col {
  display: flex;
  align-items: center;
  justify-content: center;
}

.right-col .preview {
  max-width: 400px;
}

.nav {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 6;
  overflow: hidden;
}

.nav button {
  opacity: 1;
  font-size: 2rem;

}

.nav:hover .slide-up,
.nav:hover .slide-down {
  opacity: 1;
  transform: translateX(0);
}

.nav .slide-up,
.nav .slide-down {
  display: block;
  position: absolute;
  text-align: center;
  padding: 1rem;
  opacity: 0;
  transition: 0.25s ease opacity, 0.25s ease transform;
  z-index: 99;
}

.nav .slide-up:hover,
.nav .slide-down:hover {
  opacity: 1;
}

.nav .slide-up button,
.nav .slide-down button {
  cursor: pointer;
  background: none;
  border: none;
  font-weight: 200 !important;
}

.nav .slide-up {
  top: 50%;
  left: 0;
  transform: translateX(-100%);
}

.nav .slide-down {
  top: 50%;
  right: 0;
  transform: translateX(100%);
}
@media (max-width: 768px) {
  .caption-heading h1 {
    text-align: left;
    font-size: 1.5rem; /* Mobil cihazlarda daha küçük */
    margin-bottom: 0.5rem;
  }

  .caption-subhead {
    font-size: 0.8rem; /* Mobil cihazlar için küçültüldü */
    letter-spacing: 2px;
    margin-bottom: 0.5rem;
  }
  .caption {
   
    top: 75%;
  }

}